/*    */

.overlay {

  /*    */
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 0.3s all;
}

/*     */

.modal {
  /*    */
  opacity: 0;
  visibility: hidden;

  /*      */
  width: 90%;
  max-width: 500px;

  /*     z-index */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30; /*      */

  /*      */
  box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  text-align: center;
  padding: 30px;
  border-radius: 10px;
  background-color: #fcfcfc;
  border: 2px solid #f8d24e;
  transition: 0.3s all;
}

/*        */

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
}


